import React, { Component } from 'react'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {
    Step,
    Stepper,
    StepButton,
} from 'material-ui/Stepper';
import RaisedButton from 'material-ui/RaisedButton';
import FlatButton from 'material-ui/FlatButton';
import PatientListWithoutDoctor from './AssingDoctor/PatientListWithoutDoctor'
import DoctorListForPatient from './AssingDoctor/DoctorListForPatient';
import Confirmation from './AssingDoctor/Confirmation';
import axios from 'axios';




class DoctorAssignmentToPatient extends Component {
    constructor(props) {
        super(props);

        this.state = {
            fixedHeader: true,
            stripedRows: false,
            showRowHover: false,
            selectable: true,
            multiSelectable: false,
            enableSelectAll: false,
            deselectOnClickaway: true,
            showCheckboxes: true,
            height: '480px',
            showModal: false,
            disabled: true,
            stepIndex: 0,
            visited: [],
            patientInfromationFromList: null,
            doctorInformationFromList: null

        };
    }
    componentWillMount() {
        const { stepIndex, visited } = this.state;
        this.setState({ visited: visited.concat(stepIndex) });
    }

    componentWillUpdate(nextProps, nextState) {
        const { stepIndex, visited } = nextState;
        if (visited.indexOf(stepIndex) === -1) {
            this.setState({ visited: visited.concat(stepIndex) });
        }
    }

    patientCallback = (patientDataFromList) => {
        if (!patientDataFromList) {
            return;
        } else {
            this.setState({ patientInfromationFromList: patientDataFromList });
        }
    }

    familyDoctorCallback = (doctorDataFromList) => {
        if (!doctorDataFromList) {
            return;
        } else {
            this.setState({ doctorInformationFromList: doctorDataFromList })
        }
    }

    handleNext = () => {
        const { stepIndex } = this.state;
        if (stepIndex < 2) {
            this.setState({ stepIndex: stepIndex + 1 });
        }
    };

    handlePrev = () => {
        const { stepIndex } = this.state;
        if (stepIndex > 0) {
            this.setState({ stepIndex: stepIndex - 1 });
        }
    };

    handleSubmit=()=>{


        axios.put(`http://localhost:8081/admin/doctor/${this.state.patientInfromationFromList.userName}/${this.state.doctorInformationFromList.userName}`)
            .then((response)=>{
                console.log("registration  successful");
                alert("Registracija sėkminga!");     
            })
            .catch((error)=>{
              console.log(error);
            })
            console.log(this.state);
        }
    

    // patient={this.state.patientInfromationFromList}

    getStepContent(stepIndex) {
        switch (stepIndex) {
            case 0:
                return <PatientListWithoutDoctor 
                callbackFromParentPatient={this.patientCallback} />;
            case 1:
                return <DoctorListForPatient
                    callBackFromParentDoctor={this.familyDoctorCallback} />;
            case 2:
                return <Confirmation
                    patient={this.state.patientInfromationFromList} 
                    doctor={this.state.doctorInformationFromList}/>;
            default:
                return 'You\'re a long way from home sonny jim!';
        }
    }

    render() {
        const { stepIndex, visited } = this.state;
        const contentStyle = { margin: '0 16px' };


        return (
            <MuiThemeProvider>
                <div style={{ width: '100%', maxWidth: 700, margin: 'auto' }} >

                    <Stepper linear={false} activeStep={stepIndex}>
                        <Step completed={visited.indexOf(0) !== -1} active={stepIndex === 0}>
                            <StepButton onClick={() => this.setState({ stepIndex: 0 })}>
                                Pasirinkite Pacientą
            </StepButton>
                        </Step>
                        <Step completed={visited.indexOf(1) !== -1} active={stepIndex === 1}>
                            <StepButton onClick={() => this.setState({ stepIndex: 1 })}>
                                Pasirinkite Daktarą
            </StepButton>
                        </Step>
                        <Step completed={visited.indexOf(2) !== -1} active={stepIndex === 2}>
                            <StepButton onClick={() => this.setState({ stepIndex: 2 })}>
                                Patvirtinkite
            </StepButton>
                        </Step>
                    </Stepper>
                    <div style={contentStyle}>
                        <div>{this.getStepContent(stepIndex)}</div>
                        <div style={{ marginTop: 12 }}>
                            <FlatButton
                                label="Back"
                                disabled={stepIndex === 0}
                                onClick={this.handlePrev}
                                style={{ marginRight: 12 }}
                            />
                            <RaisedButton
                                label="Next"
                                disabled={stepIndex === 2}
                                primary={true}
                                onClick={this.handleNext}
                            />
                            <RaisedButton
                                label="Submit"
                                disabled={stepIndex !== 2 }
                                primary={true}
                                onClick={this.handleSubmit}
                            />
                        </div>
                    </div>

                </div>
            </MuiThemeProvider>
        );
    }
}
export default DoctorAssignmentToPatient;


